import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./Footer.css";

interface FooterProps {
  iconName?: string;
  iconColor?: string;
}

export const Footer: React.FunctionComponent<FooterProps> = () => {
  const history = useHistory();
  useEffect(() => {
    const hash = history.location.hash;
    if (hash && document.getElementById(hash.substr(1)) !== null) {
      // Check if there is a hash and if an element with that id exists

      console.log("hash", document.getElementById(hash.substr(1)));
      document
        .getElementById(hash.substr(1))!
        .scrollIntoView({ behavior: "smooth" });
    }
  }, [history.location.hash]);
  return (
    <div className="footer" id="contactme">
      <span className="footer__icon-container">
        <a
          href="https://www.linkedin.com/in/allanman/"
          target="_blank"
          rel="noreferrer"
          className="icon-link"
        >
          <i className="fab fa-linkedin" />
        </a>
      </span>
      <span className="footer__icon-container">
        <a
          href="https://github.com/AllanRaf"
          target="_blank"
          rel="noreferrer"
          className="icon-link"
        >
          <i className="fab fa-github" />
        </a>
      </span>
    </div>
  );
};
