import React, { useEffect } from "react";
import { StackItem } from "./StackItem";
import { useHistory } from "react-router-dom";
import "./Home.css";
import "./MyStack.css";

const stackText = [
  "Pure HTML",
  "Sass, BEM and best practices.",
  "Javascript, Typescript, React, React Native.",
  "Express server, GraphQL, Rest APIs",
  "SQL, PostGreSQL",
  "Containerisation",
];

export function MyStack() {
  const history = useHistory();
  useEffect(() => {
    const hash = history.location.hash;
    if (hash && document.getElementById(hash.substr(1)) !== null) {
      // Check if there is a hash and if an element with that id exists
      document
        .getElementById(hash.substr(1))!
        .scrollIntoView({ behavior: "smooth" });
    }
  }, [history.location.hash]);
  return (
    <section className="my-stack" id="mystack">
      <div className="row">
        <StackItem
          title="HTML"
          iconName="fab fa-html5"
          description={stackText[0]}
        />
        <StackItem
          title="CSS"
          iconName="fab fa-css3-alt"
          description={stackText[1]}
          iconColor="icon-color-blue"
        />
        <StackItem
          title="Javascript"
          iconName="fab fa-js-square"
          description={stackText[2]}
          iconColor="icon-color-yellow"
        />
      </div>
      <div className="row">
        <StackItem
          title="Node"
          iconName="fab fa-node"
          description={stackText[3]}
          iconColor="icon-color-green"
        />
        <StackItem
          title="Databases"
          iconName="fas fa-database"
          description={stackText[4]}
        />
        <StackItem
          title="Docker"
          iconName="fab fa-docker"
          description={stackText[5]}
          iconColor="icon-color-light-blue"
        />
      </div>
    </section>
  );
}
