import React from "react";
import { StackTitle } from "./MyStackTitle";
import "./MyStack.css";

type StackItemProps = {
  iconName: string;
  title: string;
  description: string;
  iconColor?: string;
};

export const StackItem: React.FunctionComponent<StackItemProps> = ({
  iconName,
  title,
  description,
  iconColor,
}) => {
  return (
    <div className="icon-container">
      <StackTitle title={title} />

      <span className={`stack__fab-icon ${iconColor}`}>
        <i className={`${iconName}`} />
      </span>
      <p className="stack-text">{description}</p>
    </div>
  );
};
